@import '@/styles/public.scss';

.viewRoute-box {
  .content-box {
    height: 480px;
    .map-box {
      width: 100%;
      height: 100%;
      /* background: rosybrown; */
    }
    .content-right-box {
      position: absolute;
      top: 10px;
      right: 10px;
      height: 460px;
      width: 315px;
      background: #ffffff;
      overflow: hidden;
      box-shadow: 2px 2px 20px rgb(196, 196, 196);
      border-radius: 5px;
      .right-box {
        width: 100%;
        height: 100%;
        overflow-y: auto;
      }
    }
  }
}
