






















































































































































































































.chooseEquipment-box {
  .inspection-route-information-box {
    .inspection-title {
      height: 33px;
      line-height: 33px;
      background: #4b77be;
      font-size: 12px;
      color: #ffffff;
      text-align: center;
      border-radius: 5px;
    }

    .inspection-content {
      padding: 0 6px;
      .inspection-info-box-top {
        padding: 10px 0;
        /* border-bottom: 1px dashed #0099cc; */
        .inspection-item {
          display: flex;
          line-height: 30px;
          font-size: 12px;
          .item-left {
            margin-right: 5px;
            width: 70px;
            text-align: right;
          }
        }
      }

      .inspection-info-box-bottom {
        padding: 10px 0;
        .inspection-item-box {
          display: flex;
          justify-content: space-between;
          .inspection-item {
            width: 50%;
            display: flex;
            line-height: 30px;
            font-size: 12px;
            .item-left {
              margin-right: 5px;
              width: 60px;
              text-align: right;
            }
          }
        }
      }
    }
  }

  .equipment-pipeline-information-box {
    max-height: 460px;
    .tabs-box-route {
      .equipment-box,
      .theConduit-box {
        .table-box-route {
          .operate-button {
            color: #337ab7;
          }

          .operate-button:hover {
            color: #23527c;
            text-decoration: underline;
          }
        }
      }
    }
  }
}
